.app-wrapper {
    display: grid;
    grid-template-areas: "n" "c" "f";
    grid-template-rows: 80px 500fr 200px;
}

.app-wrapper-content {
    grid-area: c;
    background-image: url("./components/assets/images/svgReactBackground.svg");
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

a {
    text-decoration: none;
}